.link-item {
  display: block;
  margin: 5px;
  padding: 15px;
}

.link-item__link {
  display: inline-block;
  text-decoration: none;
}
.link-item__link:hover .link-item__image, .link-item__link:focus .link-item__image {
  box-shadow: 0 0 10px 0 rgba(51, 51, 51, 0.4);
}
.link-item__link:hover .link-item__text::after, .link-item__link:focus .link-item__text::after {
  width: calc(100% - 32px);
}

.link-item__image {
  height: 40px;
  background-color: #000000;
  vertical-align: middle;
  transition: all 0.3s ease-in-out;
}

.link-item__text {
  position: relative;
  display: inline-block;
  color: #333333;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
  padding: 0 0 3px;
}
.link-item__text::after {
  content: " ";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 1px;
  transition: all 0.3s ease-in-out;
  transform: translateX(-50%);
}

.link-item--site .link-item__image {
  border-radius: 50%;
  border: 1px solid transparent;
}

.link-item--site .link-item__link:hover .link-item__image, .link-item--site .link-item__link:focus .link-item__image {
  background: #0078c9;
  border-color: #0078c9;
}
.link-item--site .link-item__link:hover .link-item__text, .link-item--site .link-item__link:focus .link-item__text {
